import React from 'react';
import { motion } from 'framer-motion';
import './styles/Brands.css';

// Brand data with official image URLs
const liquorBrands = [
  {
    name: 'Jack Daniel\'s',
    image: 'https://upload.wikimedia.org/wikipedia/en/thumb/a/a8/Jack_Daniels_Logo.svg/1200px-Jack_Daniels_Logo.svg.png',
    url: 'https://www.jackdaniels.com/'
  },
  {
    name: 'Johnnie Walker',
    image: 'https://upload.wikimedia.org/wikipedia/en/4/46/JWalker_2015_logo.png',
    url: 'https://www.johnniewalker.com/'
  },
  {
    name: 'Absolut',
    image: 'https://upload.wikimedia.org/wikipedia/commons/7/71/Absolut_Vodka_Logo.svg',
    url: 'https://www.absolut.com/'
  },
  {
    name: 'Bacardi',
    image: 'https://1000logos.net/wp-content/uploads/2016/10/Bacardi-logo.jpg',
    url: 'https://www.bacardi.com/'
  },
  {
    name: 'Patrón',
    image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSF3f-cB7CULdLHRoq2FSjVfioaX6IDsIqHLRmEIsU7lohTDIy_jhYEu0uKUy64PrbymxY&usqp=CAU',
    url: 'https://www.patrontequila.com/'
  },
  {
    name: 'Grey Goose',
    image: 'https://www.greygoose.com/images/greygoose/icons/schema-logo.png',
    url: 'https://www.greygoose.com/'
  },
  {
    name: 'Hennessy',
    image: 'https://ouicognac.com/wp-content/uploads/2023/02/logo-hennessy.jpg',
    url: 'https://www.hennessy.com/'
  },
  {
    name: 'Jameson',
    image: 'data:image/jpeg;base64,',
    url: 'https://www.jamesonwhiskey.com/'
  },
  {
    name: 'Smirnoff',
    image: 'data:image/jpeg;base64,',
    url: 'https://www.smirnoff.com/'
  },
  {
    name: 'Captain Morgan',
    image: 'data:image/jpeg;base64,',
    url: 'https://www.captainmorgan.com/'
  },
  {
    name: 'Jägermeister',
    image: 'https://upload.wikimedia.org/wikipedia/en/thumb/a/ab/J%C3%A4germeister_logo.svg/1200px-J%C3%A4germeister_logo.svg.png',
    url: 'https://www.jagermeister.com/'
  },
  {
    name: 'Moët & Chandon',
    image: 'https://brandstruck.co/wp-content/uploads/2017/05/moet-chandon-logo.jpg',
    url: 'https://www.moet.com/'
  }
];

const Brands = () => {
  return (
    <section className="brands-section">
      <div className="brands-container">
        <motion.h2 
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          viewport={{ once: true }}
          className="brands-title"
        >
          Premium Brands We Carry
        </motion.h2>
        
        <motion.p 
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          transition={{ duration: 0.5, delay: 0.2 }}
          viewport={{ once: true }}
          className="brands-subtitle"
        >
          The finest selection of spirits from world-renowned distilleries
        </motion.p>
        
        <div className="brands-grid">
          {liquorBrands.map((brand, index) => (
            <motion.div
              key={brand.name}
              initial={{ opacity: 0, scale: 0.9 }}
              whileInView={{ opacity: 1, scale: 1 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
              viewport={{ once: true, margin: "0px 0px -100px 0px" }}
              className="brand-card"
            >
              <a href={brand.url} target="_blank" rel="noopener noreferrer">
                <img 
                  src={brand.image} 
                  alt={brand.name} 
                  className="brand-logo"
                  onError={(e) => {
                    e.target.onerror = null; 
                    e.target.src = '/placeholder-logo.png';
                  }}
                />
                <div className="brand-overlay">
                  <span className="brand-name">{brand.name}</span>
                </div>
              </a>
            </motion.div>
          ))}
        </div>
        
        <motion.div
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          transition={{ duration: 0.5, delay: 0.4 }}
          viewport={{ once: true }}
          className="brands-cta"
        >
          <p>Don't see your favorite brand? Ask us about special orders!</p>
        </motion.div>
      </div>
    </section>
  );
};

export default Brands;